.popup {
    position: fixed;
    z-index: 9;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: var(--theme-color-secondary-opacity-7);

    &__inner {
        display: flex;
        padding: var(--gap-3);
        align-items: center;
        justify-content: center;
        min-height: 100%;
    }

    &__container {
        width: 100%;
        max-width: 912px;
        position: relative;
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: var(--gap-4);
    }

    &__close {
        width: 24px;
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 2;

        & img, & svg {
            width: 100%;
            height: auto;
        }
    }

    &__header {
        padding-right: 40px;
    }

    &.enter {
        & .popup__container {
            opacity: 0;
        }
    }
    &.enter_active {
        & .popup__container {
            opacity: 1;
            transition: opacity 300ms;
        }
    }
    &.exit {
        & .popup__container {
            opacity: 1;
        }
    }
    &.exit_active {
        & .popup__container {
            opacity: 0;
            transition: opacity 300ms;
        }
    }
}
