.ColumnsList {
    overflow: auto;
    padding-left: 0;
    margin: 0;
    display: flex;
    gap: calc(var(--gap-3) * 1.2);
    height: 100%;
    padding: 2px var(--gap-3) var(--gap-3);
    width: 100%;
    margin: -2px 0;


    & > li {
        list-style: none;
        width: 272px;
        flex: 0 0 auto;
        height: 100%;
    }
}