@import "6_shared/assets/styles/var/breakpoints";

.CardDescription {
    &__text {
        background-color: var(--theme-color-secondary-opacity-1);
        cursor: pointer;
        min-height: 76px;
        padding: var(--gap-2);
        outline: none;
        display: block;
        border-radius: var(--border-radius-2);
        white-space: pre-wrap;

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-secondary-opacity-3);
            }
        }
    }

    &__textarea {
        background-color: var(--theme-color-secondary-opacity-1);
        cursor: pointer;
        resize: vertical;
        overflow: auto;
    
        &:focus {
            border-color: var(--theme-color-primary);
            background-color: var(--theme-color-fourth) !important;
            &::placeholder {
                opacity: 0;
            }
        }
    }
}
