@import "6_shared/assets/styles/var/breakpoints";

.dropzone_dragEnter {
    & .fileField {
        background-color: var(--theme-color-secondary-opacity-2);
        border-color: var(--theme-color-primary);
    }
}

.fileField {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem var(--gap-3);
    border-radius: var(--border-radius-2);
    background-color: var(--theme-color-secondary-opacity-1);
    height: 80px;
    cursor: pointer;
    border: 2px solid transparent;

    @media #{$mouse-device} {
        &:hover {
            background-color: var(--theme-color-secondary-opacity-3);
        }
    }
}

.preview {
    margin-top: var(--gap-3);
    display: flex;
    flex-wrap: wrap;
    gap: var(--gap-2);
    
    &:empty {
        display: none;
    }

    &__item {
        position: relative;
        flex: 0 1 calc(33.33333333% - (var(--gap-2) * 2 / 3));
        border-radius: var(--border-radius-2);
        background-color: var(--theme-color-secondary-opacity-1);
        padding: var(--gap-2);
        word-break: break-word;
        overflow: hidden;

        & img {
            width: 100%;
            height: auto;
            display: block;
            border-radius: var(--border-radius-2);
            margin-bottom: var(--gap-2);
        }

        &:hover {
            background-color: var(--theme-color-secondary-opacity-3);

            & .preview__remove {
                opacity: 1;
                pointer-events: auto;
            }
        }
    }

    &__remove {
        position: absolute;
        z-index: 4;
        top: 15px;
        left: 50%;
        color: var(--text-color-light);
        padding: var(--gap-1);
        border-radius: 50%;
        width: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--theme-color-primary);
        margin-left: -16px;
        opacity: 0;
        pointer-events: none;
        transition: opacity var(--duration-and-timing-func);
        
        & img, & svg {
            width: 100%;
            height: auto;
        }

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-primary-hover);
            }
        }
    }
}