@import "6_shared/assets/styles/var/breakpoints";

.CardComments {
    &__top {
        display: flex;
        gap: var(--gap-2);
    }

    &__list {
        margin-top: var(--gap-3);
        display: flex;
        flex-direction: column;
        gap: var(--gap-3);
    }
}

.writeCommentField {
    margin-left: calc((32px + var(--gap-2)) * -1);
    &__avatar {
        width: 32px;
        flex: 0 0 auto;
    }

    &__field {
        flex: 1 1 auto;
        position: relative;
    }

    &__textarea {
        box-shadow: var(--shadow-default);
        cursor: pointer;
        resize: vertical;

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-secondary-opacity-1);
            }
        }

        &:focus {
            background-color: var(--theme-color-fourth) !important;
        }
    }
}

.comment {
    display: flex;
    gap: var(--gap-2);
    margin-left: calc((32px + var(--gap-2)) * -1);
    border-radius: var(--border-radius-2);

    &__avatar {
        width: 32px;
        flex: 0 0 auto;
    }

    &__body {
        flex: 1 1 auto;
        position: relative;
    }

    &__info {
        display: flex;
        gap: var(--gap-2);
        align-items: flex-end;
        margin-bottom: var(--gap-2);
    }

    &__name {
        font-weight: 700;
    }

    &__date {
        color: var(--theme-color-secondary-opacity-5);
        font-size: var(--text-size-1);
        line-height: 1.42em;
    }

    &__readStatus {
        color: var(--text-color-light);
        padding: var(--gap-1);
        border-radius: var(--border-radius-1);
        background-color: var(--theme-color-success);
        font-size: var(--text-size-1);
        line-height: 1.42em;
        margin-bottom: calc(var(--gap-1) * -1);
    }

    &__text {
        box-shadow: var(--shadow-default);
        padding: var(--gap-2);
        outline: none;
        display: block;
        border-radius: var(--border-radius-2);
        border: none;
        overflow: hidden;
        white-space: pre-wrap;
        line-height: 1.42em;
        background-color: var(--theme-color-fourth);
    }

    &__actions {
        display: flex;
        gap: var(--gap-2);
        margin-top: var(--gap-1);
    }

    &__btn {
        text-decoration: underline;
        cursor: pointer;

        @media #{$mouse-device} {
            &:hover {
                opacity: 0.7;
            }
        }
    }
}