.board {
    position: relative;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background-color: var(--theme-color-secondary);

        & img, & svg {
            display: block;
            height: 100%;
            width: 100%;
            object-fit: cover;
            opacity: 0.8;
        }
    }

    &__inner {
        position: relative;
        z-index: 2;
        height: 100svh;
    }

    &__body {
        height: calc(100% - 48px);
    }
}