.ColumnsListPreload {
    padding-left: 0;
    margin: 0;
    display: flex;
    gap: var(--gap-3);
    padding: var(--gap-3);

    & > li {
        list-style: none;
        width: 272px;
        border-radius: var(--border-radius-3);
        background-color: var(--theme-color-primary-opacity-5);
        animation: flashing 1.5s ease infinite;
        &:nth-child(1) {
            height: 300px;
        }
        &:nth-child(2) {
            height: 500px;
        }
        &:nth-child(3) {
            height: 400px;
        }
    }
}

@keyframes flashing {
    0% {background-color: var(--theme-color-primary-opacity-5);}
    50% {background-color: var(--theme-color-primary-opacity-7);}
    100% {background-color: var(--theme-color-primary-opacity-5);}
}