@import "6_shared/assets/styles/var/breakpoints";

.CardImages {
    display: flex;
    flex-direction: column;
    gap: var(--gap-2);
    &__list {
        display: flex;
        gap: var(--gap-3);
        flex-wrap: wrap;

        & > * {
            flex: 0 1 calc(50% - (var(--gap-3) / 2));
        }
    }

    &__form {

    }
}

.image {
    display: flex;
    gap: var(--gap-2);
    align-items: center;
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius-3);

    &__container {

        flex: 0 0 auto;
        width: 112px;
        height: 80px;
        cursor: pointer;
        transition: opacity var(--duration-and-timing-func);

        @media #{$mouse-device} {
            &:hover {
                opacity: 0.7;
            }
        }

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: var(--border-radius-2);
        }
    }

    &__name {
        font-weight: 700;
        padding-left: var(--gap-2);
    }
}