.row {
    display: flex;
    gap: var(--gap-3);

    &:not(:first-child) {
        margin-top: var(--gap-4);
    }

    &__icon {
        flex: 0 0 auto;
        width: 28px;
        margin-top: -4px;

        & img, & svg {
            width: 100%;
            height: auto;
        }
    }

    &__body {
        flex: 1 1 auto;
    }
}