@import "./styles/normalize.scss";
@import "../6_shared/assets/styles/var/breakpoints.scss";
:root {
    /* Fonts */
    --theme-font-primary: "Roboto", sans-serif;

    /* Theme coloro */
    $opacitySteps: (
        "1": 0.04,
        "2": 0.06,
        "3": 0.1,
        "4": 0.2,
        "5": 0.3,
        "6": 0.6,
        "7": 0.7,
        "8": 0.8,
        "9": 0.9
    );
    --theme-color-primary: rgb(12, 102, 228);
    @each $i, $value in $opacitySteps {
        --theme-color-primary-opacity-#{$i}: rgba(12, 102, 228, #{$value});
    }

    --theme-color-primary-hover: #0055cc;

    --theme-color-secondary: rgb(9, 30, 67);
    @each $i, $value in $opacitySteps {
        --theme-color-secondary-opacity-#{$i}: rgba(9, 30, 67, #{$value});
    }

    --theme-color-success: rgb(31, 132, 90);
    @each $i, $value in $opacitySteps {
        --theme-color-tertiary-opacity-#{$i}: rgba(31, 132, 90, #{$value});
    }

    --theme-color-fourth: rgb(255, 255, 255);
    @each $i, $value in $opacitySteps {
        --theme-color-fourth-opacity-#{$i}: rgba(255, 255, 255, #{$value});
    }

    --theme-color-error: rgb(255, 0, 0);

    --theme-main-gradient: linear-gradient(97.78deg, #6e5dc6 17.5%, #6e5dc6 113.39%);

    /* Text color */
    --text-color-dark: #172b4d;
    --text-color-light: #ffffff;
    --text-color-error: rgb(255, 0, 0);
    --text-color-success: rgb(31, 132, 90);
    --text-color-selected: #0c66e4;

    /* Text Size */
    --text-size-1: 10px;
    --text-size-2: 14px;
    --text-size-3: 18px;
    --text-size-4: 22px;
    --text-size-5: 26px;

    /* Shadow color*/
    --shadow-default: 0px 1px 1px #091e4240, 0px 0px 1px #091e424f, 0px 1px 1px #091e4240, 0px 0px 1px #091e424f;

    /* Step */
    --step: 4px;

    /* Border-radius */
    --border-radius-1: calc(var(--step));
    --border-radius-2: calc(var(--step) * 2);
    --border-radius-3: calc(var(--step) * 3);

    /* Gaps */
    --gap-0: 0;
    --gap-1: calc(var(--step) * 1);
    --gap-2: calc(var(--step) * 2);
    --gap-3: calc(var(--step) * 4);
    --gap-4: calc(var(--step) * 8);
    --gap-5: calc(var(--step) * 16);

    /* Transition duration */
    --duration-and-timing-func: 0.15s ease;
}

@import "../6_shared/assets/styles/default.scss";

*,
*::after,
*::before {
    box-sizing: border-box;
}

html {
    font-size: 10px;
}

body {
    font-family: var(--theme-font-primary);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42em;
    color: var(--text-color-dark);
    background-color: var(--theme-color-fourth);

    scrollbar-color: var(--theme-color-primary-opacity-9) transparent;
    scrollbar-width: thin;
    scrollbar-arrow-color: transparent;
}

::-webkit-scrollbar {
    width: 4px;
}

::-webkit-scrollbar:horizontal {
    height: 8px;
}

::-webkit-scrollbar-thumb {
    background-color: var(--theme-color-primary-opacity-9);
    border-radius: 8px;
}

button {
    cursor: pointer;
    background-color: transparent;
    border: none;
    border-radius: 0;
    padding: 0;
    outline: none;
    color: inherit;

    &[disabled] {
        pointer-events: none;
        opacity: 0.7;
    }
}

h1,
.title-1 {
    margin: 0;
    font-weight: 700;
    font-size: var(--title-size-h1);
}
h2,
.title-2 {
    margin: 0;
    font-weight: 700;
    font-size: 26px;
}
h3,
.title-3 {
    margin: 0;
    font-weight: 700;
    font-size: 22px;
}
h4,
.title-4 {
    margin: 0;
    font-weight: 700;
    font-size: 18px;
}
h5,
.title-5 {
    margin: 0;
    font-weight: 700;
    font-size: 14px;
}
h6,
.title-6 {
    margin: 0;
    font-weight: 700;
    font-size: var(--title-size-h6);
}

.container {
    width: 100%;
    max-width: 865px;
    margin: 0 auto;
    padding: 0 20px;
}
