@import "6_shared/assets/styles/var/breakpoints";

.CardPrevie {
    position: relative;
    background-color: var(--theme-color-fourth);
    border-radius: var(--border-radius-2);
    box-shadow: var(--shadow-default);
    cursor: pointer;


    @media #{$mouse-device} {
        &:hover {
            outline: solid 2px var(--theme-color-secondary);
        }
    }

    &__cover {
        height: 140px;
        padding-bottom: var(--gap-2);

        & img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            border-radius: var(--border-radius-2) var(--border-radius-2) 0 0;
        }
    }
    &__inner {

    }

    &__info {
        display: flex;
        align-items: center;
        gap: var(--gap-3);
        padding: var(--gap-2);
    }

    &__info_item {
        display: flex;
        gap: var(--gap-1);

        & svg, & img {
            height: 18px;
            width: auto;
            flex: 0 0 auto;
        }
    }

    &.dragging {
        &:before {
            position: absolute;
            content: '';
            z-index: 2;
            background-color: var(--theme-color-primary-opacity-2);
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border-radius: var(--border-radius-3);
        }
        & .CardPrevie__inner, & .CardPrevie__cover, & .CardPrevie__info {
            visibility: hidden;

            & > * {
                pointer-events: none;
            }
        }
    }
}

.statusLabel {
    color: var(--text-color-light);
    padding: var(--gap-1);
    border-radius: var(--border-radius-1);
    background-color: var(--theme-color-success);
    font-size: var(--text-size-1);
    line-height: 1em;
    display: inline-flex;
    align-items: center;
}