.dragging {
    background-color: var(--theme-color-primary-opacity-5);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: var(--border-radius-3);
}
.column {
    height: 100%;


    &__inner {
        max-height: 100%;
        border-radius: var(--border-radius-3);
        overflow: hidden;
        box-shadow: var(--shadow-default);
        position: relative;
        --padding: 2;
        padding: calc(var(--step) * var(--padding));
        background-color: #f1f1f1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &.dragging {
            background-color: var(--theme-color-primary-opacity-5);
            & > * {
                opacity: 0;
            }
        }
    }

    &__head {
        cursor: pointer;
        padding-bottom: var(--gap-2);
    }

    &__body {
        height: 100%;
        overflow-x: visible;
        overflow-y: auto;
        padding: 2px 4px 2px 2px;
        display: flex;
        flex-direction: column;
        gap: var(--gap-2);
        margin: 0 -4px 0 -2px;
    }

    &__footer {
        padding-top: var(--gap-2);
    }
}