.CreateCardButton {
    &__textarea {
        height: 76px;
        border-radius: var(--border-radius-2);
        background-color: var(--theme-color-fourth);
        box-shadow: var(--shadow-default);
        width: 100%;
        border: 2px solid transparent;
        padding: var(--gap-1);
        resize: vertical;
        outline: none;

        &:focus {
            border-color: var(--theme-color-primary);
            box-shadow: none;
            &::placeholder {
                opacity: 0;
            }
        }

        &.error {
            border-color: var(--theme-color-error) !important;
            color: var(--text-color-error);
            box-shadow: none;
        }
        
        &.error::placeholder {
            color: var(--text-color-error);
        }
    }
}