.CardePreviewActions {
    position: relative;

    &:hover {
        & .CardePreviewActions__buttons {
            opacity: 1;
            pointer-events: auto;
        }
    }

    &__buttons {
        position: absolute;
        z-index: 2;
        top: var(--gap-1);
        right: var(--gap-1);
        display: flex;
        opacity: 0;
        pointer-events: none;
    }

    &__btn {
        flex: 0 0 auto;
    }

    &__body {
        position: relative;
        z-index: 1;
        padding: var(--gap-2);

        & p {
            margin: 0;
            word-break: break-word;
        }
    }

    &__textarea {
        position: relative;
        z-index: 1;
    }
}