.profile {
    display: flex;
    flex-direction: column;
    gap: var(--gap-3);
    &__head {}
    &__body {}
}

.head {
    display: flex;
    align-items: center;
    gap: var(--gap-3);
    font-size: var(--text-size-3);

    &__photo {
        flex: 0 0 140px;
    }

    &__text {
        flex: 1 1 auto;
    }

    &__login {
        font-weight: 700;
    }

    &__email {
        margin-top: var(--gap-1);
    }
}