@import "6_shared/assets/styles/var/breakpoints";

.board {
    display: flex;
    height: 100%;
    &__side {
        flex: 0 0 260px;
    }
    &__columns {
        flex: 0 0 auto;
        width: calc(100% - 260px);
        height: 100%;
    }
}

.side {
    background-color: #09326ce6;
    color: #fff;

    &__head {
        padding: var(--gap-3);
        font-size: var(--text-size-3);
        font-weight: 700;
        border-bottom: 1px solid var(--theme-color-fourth-opacity-4);
    }

    &__body {
        display: flex;
        flex-direction: column;
        gap: var(--gap-3);
    }

    &__list {
        padding-left: 0;
        margin-bottom: 0;
        list-style: none;

        & li {
            list-style: none;
            position: relative;
        }
    }

    &__list_item {
        text-decoration: none;
        color: var(--text-color-light);
        padding: var(--gap-2) var(--gap-3);
        display: flex;
        align-items: center;
        gap: var(--gap-2);
        width: 100%;

        & svg, & img {
            flex: 0 0 auto;
            height: 18px;
            width: auto;
        }

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-fourth-opacity-3);
            }
        }
    }
}