@import "6_shared/assets/styles/var/breakpoints";

.user_list {
    padding-left: 0;
    margin-bottom: 0;
    overflow: auto;
    max-height: 500px;

    & > li {
        list-style: none;
        &:not(:first-child) {
            margin-top: var(--gap-3);
        }
    }
}

.user {
    display: flex;
    align-items: center;
    gap: var(--gap-3);

    &__photo {
        flex: 0 0 auto;
        width: 52px;
    }

    &__info {
        flex: 1 1 auto;
    }

    &__login {
        font-weight: 700;
        font-size: var(--text-size-2);
    }

    &__email {
        //margin-top: var(--gap-1);
    }

    &__icon {
        flex: 0 0 auto;
        width: 28px;
        padding: var(--gap-1);
        border-radius: var(--border-radius-1);
        color: var(--text-color-success);

        & img, & svg {
            width: 100%;
            height: auto;
            display: block;
        }
    }

    &__btn {
        cursor: pointer;
        color: var(--text-color-dark);
        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-secondary-opacity-3);
            }
        }
    }
}