@import "6_shared/assets/styles/var/breakpoints";

.CreateColumnButton {
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius-3);

    &__inner {
        cursor: pointer;
        text-align: center;
    }

    &__box {
        background-color: var(--theme-color-fourth-opacity-6);

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-fourth-opacity-8);
            }
        }
    }
}
