@import "6_shared/assets/styles/var/breakpoints";

.members {
    position: absolute;
    top: 0;
    left: calc(100% + 16px);
    z-index: 10;
    background-color: var(--theme-color-fourth);
    border-radius: var(--border-radius-2);
    box-shadow: 0 8px 12px #091e4226,0 0 1px #091e424f;
    padding: var(--gap-3);
    width: 260px;
    color: var(--text-color-dark);

    &__head {
        padding-right: 25px;
    }

    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
        padding: var(--gap-1);
        border-radius: var(--border-radius-1);
        cursor: pointer;

        & img, & svg {
            width: 100%;
            height: auto;
            display: block;
        }

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-secondary-opacity-3);
            }
        }
    }

    &__list {
        padding-left: 0;
        margin-bottom: 0;
    }
}

.user {
    display: flex;
    align-items: center;
    padding: var(--gap-1);
    gap: var(--gap-2);

    &__photo {
        flex: 0 0 auto;
        width: 22px;
    }

    &__remove {
        margin-left: auto;
        flex: 0 0 auto;
    }

    &__remove_btn {
        width: 20px;
        padding: var(--gap-1);
        border-radius: var(--border-radius-1);
        cursor: pointer;

        & img, & svg {
            width: 100%;
            height: auto;
            display: block;
        }

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-secondary-opacity-3);
            }
        }

        &.text {
            font-size: var(--text-size-1);
            width: auto;
        }
    }
}