.boardsLoader {
    display: flex;
    flex-wrap: wrap;
    gap: var(--gap-3);
}

.item {
    display: block;
    position: relative;
    min-height: 96px;
    flex: 0 1 calc(25% - (var(--gap-3) * 3 / 4));
    border-radius: var(--border-radius-1);
    overflow: hidden;
    background-color: var(--theme-color-secondary-opacity-2);
    animation: anim 1.5s ease infinite;
}

@keyframes anim {
    0% {background-color: var(--theme-color-secondary-opacity-2);}
    50% {background-color: var(--theme-color-secondary-opacity-3);}
    100% {background-color: var(--theme-color-secondary-opacity-2);}
}