@import "6_shared/assets/styles/var/breakpoints";

.createBoard {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: var(--gap2);
    min-height: 96px;
    background-color: var(--theme-color-secondary-opacity-2);
    width: 100%;
    transition: background-color var(--duration-and-timing-func);

    @media #{$mouse-device} {
        &:hover {
            background-color: var(--theme-color-secondary-opacity-3);
        }
    }
}