@import "6_shared/assets/styles/var/breakpoints";

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--gap-2);
    padding: var(--gap-2);
    min-height: 48px;
    background-color: #fff;
    border-bottom: 1px solid var(--theme-color-secondary-opacity-3);

    &.dark {
        background-color: var(--theme-color-secondary);

        & .logo svg path {
            fill: #fff !important;
        }
    }
}

.logo {
    width: 75px;

    & img, & svg {
        width: 100%;
        height: auto;
        display: block;
    }
}

.profile {
    position: relative;
    width: 30px;
    cursor: pointer;
}

.menu {
    position: absolute;
    top: calc(100% + 10px);
    right: 0;
    background-color: var(--theme-color-fourth);
    border-radius: var(--border-radius-3);
    overflow: hidden;
    box-shadow: var(--shadow-default);
    z-index: 3;

    &__inner {
        background-color: var(--theme-color-secondary-opacity-1);
        padding: var(--gap-2);
        min-width: 232px;
    }

    &__name {
        font-weight: 700;
    }

    &__email {
        
    }

    &__list {
        margin-top: var(--gap-3);
        padding-left: 0;
        margin-bottom: 0;

        & > li:not(:first-child) {
            list-style: none;
            margin-top: var(--gap-1);
        }
    }

    &__item  {
        cursor: pointer;
        text-decoration: none;
        
        @media #{$mouse-device} {
            &:hover {
                opacity: 0.7;
            }
        }
    }
}