@import "6_shared/assets/styles/var/breakpoints";

.boardsList {
    &.grid {
        display: flex;
        flex-wrap: wrap;
        gap: var(--gap-3);

        & .item {
            display: block;
            position: relative;
            min-height: 96px;
            flex: 0 1 calc(25% - (var(--gap-3) * 3 / 4));
            border-radius: var(--border-radius-1);
            overflow: hidden;
            text-decoration: none;
        
            @media #{$mouse-device} {
                &:hover {
                    & .item__bg {
                        &::before {
                            background-color: var(--theme-color-secondary-opacity-5);
                        }
                    }
                }
            }
        
            &__bg {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
        
                & img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    position: relative;
                    z-index: 1;
                }
        
                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 2;
                    background-color: var(--theme-color-secondary-opacity-4);
                    opacity: 1;
                    transition: background-color var(--duration-and-timing-func);
                }
            }
        
            &__name {
                position: relative;
                z-index: 2;
                padding: var(--gap-3);
                color: var(--text-color-light);
                font-weight: 700;
                font-size: var(--text-size-3);
                word-break: break-word;
            }
        }
    }

    &.list {
        & .item {
            display: flex;
            align-items: center;
            gap: var(--gap-2);
            padding: var(--gap-2) var(--gap-3);
            color: inherit;
            text-decoration: none;

            @media #{$mouse-device} {
                &:hover {
                    background-color: var(--theme-color-fourth-opacity-3);
                }
            }

            &.active {
                background-color: var(--theme-color-fourth-opacity-4);
            }

            &__bg {
                flex: 0 0 20px;
                height: 20px;
                overflow: hidden;
                border-radius: var(--border-radius-1);

                & img, & svg {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }

            &__name {
                word-break: break-word;
            }
        }
    }
}

