.ColumnHead {
    display: flex;
    gap: var(--gap-3);
    align-items: center;

    &__title {
        flex: 1 1 auto;
        font-size: var(--text-size-2);
        font-weight: 700;

        & > p {
            margin: 0;
            padding: 0 var(--gap-3);
        }
    }

    &__button {
        flex: 0 0 auto;
    }
}